/* COLORS */
$white: #FFFFFF;
$light-grey: #F6F4F4;
$beige-bright: #A89B94;
$beige-medium: #918178;
$beige-dark: #655953;
$blue-dark: #2F4858;
$green-dark: #2F474B;
$orange-brown: #A66A5E;
$brown: #A28967;
$black: #000000;
$red: #D40D0D;
$green: #049f44;

// COLORS WITH TRANSPARENCY
$transparent-white: transparentize($color: $white, $amount: 0.40);

* {
	font-family: "Heebo", Helvetica, sans-serif;
	font-weight: 300;
	line-height: 1.5;
}

.heading, h1, h2, h3, h4, h5 {
	font-family: 'Playfair Display', serif;
  font-weight: 400;
  color: $blue-dark;
}

.heading--h1 {
  font-size: 80px;
  font-weight: 600;	
	line-height: 1.2;
}

.heading--h2 {
  font-size: 240px;
  font-weight: 400;
	line-height: 1;
	opacity: 0.08;
	white-space: nowrap;
	text-transform: uppercase;
}

.heading--h3 {
  font-size: 48px;
  font-weight: 400;
	line-height: 1.2;
}

.heading--h4 {
  font-size: 20px;
  font-weight: 400;
	line-height: 1.2;
}

.heading--p {
	font-size: 18px;
	font-weight: 300;
	font-family: "Heebo", Helvetica, sans-serif;
}

.paragraph, p {
	font-size: 18px;
	font-weight: 300;
	font-family: "Heebo", Helvetica, sans-serif;

	&--bold { font-weight: 600; }
	&--small { font-size: 14px; }
	&--big { font-size: 32px; font-family: "Playfair Display"; }

}

.link, a {
	font-family: "Heebo", Helvetica, sans-serif;
	font-size: 18px;
	line-height: 26px;
	line-height: 1.2;
	text-decoration: none;
	font-weight: 300;

	&--small {
		font-size: 14px;
	}

	&--serif {
		font-family: 'Playfair Display', serif;
		font-weight: 400;
		font-size: 32px;
	}

}

.highlight {
	font-weight: inherit;
	font-family: inherit;
}

.text-input__input, 
.textarea__input {
	font-size: 18px;
	font-weight: 300;
}

.text-input__label, 
.textarea__label {
	font-size: 18px;
	font-weight: 300;
}


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.body {
	width: 100%;
	max-width: 100vw;
	overflow-x: hidden;

	&--dark { background-color: $green-dark; }
	&--light { background-color:$light-grey;  }

}

img {
	-webkit-user-drag: none;
}



.contact-form {
	display: flex;
	flex-direction: column;
	gap: 16px;

	&__disclaimer, .disclaimer {

		&__text {
			color: $white;
		}

		&__link {
			color: white;
			text-decoration: underline;
			font-weight: inherit;
		}

	}

	&__submit {
		align-self: flex-end;
	}

	&__status-message, .status-message {

		&--success, 
		&--error {
			display: none;
			color: $white;
			text-align: center;
			position: relative;	

			&::after {
				content: "";
				position: absolute;
				width: 4px;
				height: 4px;
				top: 12px;
				display: inline-block;
				margin-left: 8px;
				background-color: $red;
				border-radius: 50%;
			}

		}

		&--success::after { background-color: $green; }
		&--error::after { background-color: $red; }

		&--active { display: block }

	}

}
.hero {
	width: 100%;
	height: 100%;
	max-width: 100vw;
	min-height: 100vh;
	
	&__inner {
		display: grid;
		grid-template: auto / 400px auto;
	}

	&__panel, .panel {
		width: 400px;
		min-height: 100vh;
		padding: 56px 48px 56px 96px;
		background-color: $blue-dark;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		z-index: 30;

		&__logo {
			margin-bottom: 80px;
			width: 160px;
		}

		&__heading {
			margin-bottom: 64px;
			width: calc(100vw - 320px);
			max-width: 800px;
		}

		&__teaser {
			margin-bottom: 32px;
		}

		&__scroller {
			color: $white;
			align-self: center;
			justify-self: flex-end;
			margin-top: auto;
			font-size: 64px;
			margin-right: 48px;
			cursor: pointer;
		}

	}

	&__showcase, .showcase {
		position: relative;
		max-width: calc(100vw - 400px);
		height: 100%;

		&__navigation, .navigation {
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			display: flex;
			padding: 32px;
			z-index: 30;

			&__link {
				cursor: pointer;
			}

			&__button {
				margin-left: auto;
				cursor: pointer;
			}

		}

		&__slider, .slider {
			height: 100%;
			width: 100%;

			&__wrapper {
				display: flex;
				flex-direction: row;

			}

			&__image {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

		}

	}

}


.swiper {
	width: 100%;
	height: 100%;
}

.swiper-slide {
	background-position: center;
	background-size: cover;
}

.swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.intro {

	&__image {
		display: block;
		width: 100%;
		clip-path: inset(0% 10px 10px 10px);
		transition: all 0.6s ease-in-out;
		clip-path: inset(0% 50% 0% 0%);
		overflow: hidden;
	}

}
.references {
  max-width: 100%;
  background-color: #655953;
  overflow: hidden;
  
  
  &__inner {
    display: grid;
    grid-template-areas:
		"heading heading"
    "description slider"
    "controls slider";
    grid-template-columns: 400px 100%;
    grid-template-rows: 320px auto 120px;
    min-height: 1120px;
		padding-bottom: 80px;
  };

  &__heading {
   grid-area: heading; 
	 position: relative;
	 margin-bottom: 64px;

	 &::after {
		content: "";
		position: absolute;
		width: 33%;
		height: 2px;
		background-color: $white;
		z-index: 5;
		top: calc(100% - 2px);
		left: 64px;
	 }

  }
  
  &__slider, .slider { 
    grid-area: slider;
    max-width: unset;
    width: 100%;
    
    &__container {
      height: 100%;
      display: flex;
    }
    
    &__wrapper {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    
    &__item, .item {
      height: calc(100% - 80px);
      margin-left: 32px;
			width: 200px;
      margin-right: 0 !important;

      &__image {
        height: 100%;
        display: block;
        object-fit: cover;
      }

      &__heading {
        color: #FFFFFF;
				margin-top: 16px;
      }

      &:nth-child(2n) {
        margin-top: -80px;
      }

    }
    
  }

  &__controls, .controls {
    grid-area: controls;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: rgba(101, 89, 83, 0.95);
    z-index: 10;
		padding: 32px 64px;
    
    &__scrollbar {
      width: 100% !important;
      height: 4px;

      
      .swiper-scrollbar-drag {
        background: #FFFFFF;
      }
      
    }
    
    &__arrow-wrapper {
      margin-left: 16px;
      height: 16px;
      width: 48px;
      position: relative;
    }
    
    &__arrow {
      width: 16px;
      height: 16px;
      margin-top: unset;
      top: 0;
      
      &--prev {
        left: 0;
      }
      
      &--next {
        right: 0;
      }
      
      
      &::after {
        font-size: 16px;
        height: 16px;
        color: white;
      }

    }

  }
  
  &__description, .description {
    z-index: 10;
    width: auto;
    width: 100%;
    background-color: rgba(101, 89, 83, 0.95);
    grid-area: description;
    padding: 64px;
    
    &__text {
      color: white;
    }

  }

}

.circle-link {
	color: $white;
	display: flex;
	flex-direction: row;
	align-items: center;

	&::after { 
		content: "\ea03";
		margin-left: 16px;
		font-family: Icon;
		transform: rotate(270deg);
		width: 32px;
		height: 32px;
		display: flex;
		cursor: pointer;
		align-items: center;
		font-size: 24px;
		justify-content: center;
		vertical-align: center;
		text-align: center;
		color: $white !important;
		padding: 8px;
		border: 1px solid $white;
		border-radius: 50%;
	}

	&--arrow::after { 
		content: "\ea03";
		transform: rotate(235deg);
	}

	&--close::after { 
		content: "\ea05"; 
	}
	
}
.cursor {
	pointer-events: none;

	&__main {
		position: fixed;
		top: -100px;
		left: -100px;
		width: 8px;
		height: 8px;
		background-color: transparent;
		transform: translate(-50%, -50%);
		border-radius: 50%;
		border: 1px solid $white;
		z-index: 20;
	}
	
	&__follower {
		position: fixed;
		top: -100px;
		left: -100px;
		width: 32px;
		height: 32px;
		transform: translate(-50%, -50%);
		background-color: $orange-brown;
		border-radius: 50%;
	}
		
}
.external-link {
	border-bottom: 1px solid $beige-medium;
	color: $beige-medium;
	font-weight: 300;
	cursor: pointer;
	font-size: 24px;

	&__icon {
		transform: rotate(230deg);
		position: relative;
		right: -4px;
		font-size: 24px !important;
		color: $beige-medium !important;
	}

}
.heading {
  color: $blue-dark;
	 
  &--black { color: $black }
  &--beige-dark { color: $beige-dark }
  &--blue-dark { color: $blue-dark }
	&--white { color: $white }

	.heading--h2 {
		
		&--beige { color: $beige-medium }
		&--white { color: $white }
		
	}

}
.icon {
  display: inline-block;
  font: normal normal normal 32px 'Icon';
  color: inherit;
  flex-shrink: 0;
	color: $beige-dark;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon--small { font-size: 12px; }
.icon--medium { font-size: 16px; }
.icon--large { font-size: 24px; }
.icon--big { font-size: 32px; }

.icon--angle::before { content: "\ea02"; }
.icon--arrow::before { content: "\ea03"; }
.icon--linkedin::before { content: "\ea04"; }
.icon--close::before { content: "\ea05"; }
.highlight {
	color: $black;

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
 .gravity-link {
	 padding: 16px;
	 color: $white;
	 cursor: pointer;
	 display: inline-block;

	 &--black { color: $black; }
	 &--beige-dark { color: $beige-dark; }
	 &--blue-dark { color: $blue-dark; }
	 &--white { color: $white; }

 }
.paragraph {

  &--black { color: $black; }
  &--beige-dark { color: $beige-dark; }
  &--beige-medium { color: $beige-medium; }
  &--blue-dark { color: $blue-dark; }
	&--white { color: $white; }

}
.quote {

	&::before { content: "\“"; }
	&::after { content: "\”"; }

}
.switcher {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	width: 280px;
	height: 120px;

	&__label, .label {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px) brightness(1.2);

		&__text {
			color: $white;
		}

		&__icon {
			transform: rotate(270deg);
			color: $white;
			position: relative;
			left: 0;
			transition: left 0.1s ease-in-out;
		}

	}

	&:hover {

		.label__icon {
			left: 8px;
		}

	}

}
.text-input {
	width: 100%;
	position: relative;
	
	&__input {
		width: 100%;
		height: 48px;
		color: $white;
		padding: 8px 0;
		outline: none;
		border: none;
		border-bottom: 1px solid $transparent-white;
		padding: 0;
		background-color: transparent;
		margin-top: 40px;

		&:focus + .text-input__label,
		&:not(:placeholder-shown) + .text-input__label,
		&--active + .text-input__label {
			transform: translate(-16px, -32px);
			font-size: 12px;
		}

		&--error {
			border-bottom: 1px solid $red;
			
			& ~ .text-input__error-message {
				display: block;
			}
			
		}
	}
	
	&__label {
		position: absolute;
		color: $transparent-white;
		bottom: calc(50% - (24px / 2));
		top: 48px;
		left: 16px;
		transition: all 0.2s ease;
		pointer-events: none;
	}

	&__error-message {
		color: $red;
		padding-top: 8px;
		display: none;
	}

}
.textarea {
	width: 100%;
	position: relative;
	
	&__input {
		width: 100%;
		display: block;
		height: 120px;
		color: $white;
		outline: none;
		border: none;
		border-bottom: 1px solid $transparent-white;
		padding: 0;
		background-color: transparent;
		margin-top: 40px;
		resize: none;
		
		&:focus + .textarea__label,
		&:not(:placeholder-shown) + .textarea__label,
		&--active + .textarea__label {
			transform: translate(-16px, -32px);
			font-size: 12px;
		}

		&--error {
			border-bottom: 1px solid $red;
			
			& ~ .text-input__error-message {
				display: block;
			}
			
		}

	}
	
	&__label {
		position: absolute;
		color: $transparent-white;
		top: 48px;
		left: 16px;
		transition: all 0.2s ease;
		pointer-events: none;
	}

	&__error-message {
		color: $red;
		padding-top: 8px;
		display: none;
	}

}